import React from 'react'
import Check from './check'
import { data } from './data'
import { connexion } from './useConnect'

export const roleTypeList = {
  USER: 'USER',
  MODERATOR: 'MODERATOR',
  ADMIN: 'ADMIN',
  DEV: 'DEV',
  MANAGER: 'MANAGER'
}

export const connexionSearch = (fetchData, condition, success, fail) => {
  connexion(fetchData, condition, success, fail)
}

export const dataConnect = data

export const CheckConnexion = ({ bool, role, children }) => {
  return <Check bool={bool} roleType={role} children={children}></Check>
}
